<template>
  <div>
    <Banner :contents="bannerData.contents"></Banner>
    <ItemTitle title="我们的产品" smallTitle="our product"></ItemTitle>
    <ItemIntro :contents="itemItroData.contents"></ItemIntro>
    <ItemTitle title="客户案例" smallTitle="Customer case"></ItemTitle>
    <ItemEx></ItemEx>
    <ItemTitle title="公司动态" smallTitle="Company News"></ItemTitle>
    <ItemDetail></ItemDetail>
  </div>
</template>
<script>
  import {mapState} from 'vuex'
////////////////////////////////////////////////
  import Banner from './Banner.vue'
  import ItemTitle from './Item_title.vue'
  import ItemIntro from './Item_introduction.vue'
  import ItemEx from './Item_ex.vue'
  import ItemDetail from './Item_detail.vue'
  export default {
    components:{
      Banner,
      ItemTitle,
      ItemIntro,
      ItemEx,ItemDetail
    },
    computed:mapState([
      "bannerData",
      "itemItroData"
    ])
  }
</script>



