<template>
  <div class="product">
    <BannerMsg :item="item" v-for="(item,index) in contents" :key="index"></BannerMsg>
    <div class="content container">
      <span class="titleS">深圳</span>
      <dl v-for="(item,index) in joinData" :key="index">
        <dt><img :src="item.imgUrl" alt="imgback" class="title-img"> {{item.classify}}</dt>
        <dd v-for="(item,num) in item.job" :key="num">
          <span @click="dialog(index,num,item.name)" class="ddS">{{item.name}}</span>
        </dd>
      </dl>
      
      <el-dialog :visible.sync="dialogVisible" width="40%" :title="name">
        <div class="alertD">
          <div class="demand">
            <span class="address">工作地点：{{joinData[i].job[j].children.address}}</span>
            <span class="num">招聘人数：{{joinData[i].job[j].children.num}}</span>
          </div>
          <div class="cont"  v-for="(cont,num) in joinData[i].job[j].children.demand" :key="num">
            <h5>{{cont.classify}}</h5>
            <ul>
              <ol>{{cont.classCont}}</ol>
            </ul>
          </div>
          <p class="contact">联系我们，我们期待您的加入！邮箱：<span class="email">hr@xxxxx.com</span></p>
        </div>
      </el-dialog>
    </div>
  </div>
</template>
<script>
import BannerMsg from '../home/BannerMsg.vue'
import {mapState} from 'vuex'
import backimg3 from '../../assets/img/backimg3.jpg'
export default {
  data() {
    return {
      dialogVisible: false,
      contents:[
        { title: "加入我们", text: "加入我们加入我们加入我们", imgUrl: backimg3}
      ],
      name:'',
      i:0,j:0
    };
  },
  computed:mapState([
    "joinData"
  ]),
  components:{
    BannerMsg
  },
  methods:{
    dialog(i,j,name){
      this.dialogVisible = true;
      this.name = name;
      this.i = i;
      this.j = j;
    }
  }
};
</script>
<style>
.alertD {
  font-size: 12px;
  color: #666;
  text-align: left;
}
.alertD .title,.el-dialog__title {
  font-size: 14px;
  text-align: center;
  font-weight: bold;
}
.alertD .address {
  margin-right: 50px;
}
.alertD h5 {
  margin: 10px 0;
}
.alertD ol {
  padding: 0;
  display: inline;
}
.alertD .contact {
  text-align: center;
  margin-top: 30px;
}
.alertD .email {
  color: #008aff;
}

.content {
  text-align: center;
  margin: 20px auto;
}
.content .titleS {
  font-size: 12px;
  padding: 8px 20px;
  background-color: #008aff;
  color: #fff;
  letter-spacing: 2px;
}
.content dl {
  margin: 80px 0;
}
.content dt {
  font-size: 20px;
  color: #666;
  margin: 30px 0;
}
.content dt .title-img {
  height: 26px;
  margin-right: 20px;
  vertical-align: middle;
}
.content dd {
  margin: 20px 0;
}
.content .ddS {
  display: inline-block;
  height: 45px;
  line-height: 45px;
  font-size: 16px;
  width: 300px;
  text-align: center;
  background-color: #eceff4;
  color: #333;
  cursor: pointer;
}
.content .ddS:hover {
  background-color: #dbdee3;
}
</style>



