import Vue from 'vue'
import Vuex from 'vuex'
import homeData from '../data/homeData.js'
import bannerData from '../data/bannerData.js'
import itemItroData from '../data/itemItroData.js'
import proitem1Data from '../data/proitem1Data.js'
import joinData from '../data/joinData.js'
Vue.use(Vuex)
const store = new Vuex.Store({
  state: {
    homeData,
    bannerData, itemItroData,proitem1Data,joinData
  }
})
export default store



