<template>
  <div class="product">
    <BannerMsg :item="contents[num]"></BannerMsg>
    <!-- <router-view name="a" :item="contents[num]"></router-view> -->
    <ul class="proNav container">
      <router-link :to="item.url" tag="li" @click.native="overTag(index)" v-for="(item,index) in contents" :key="index">{{item.title}}</router-link>
    </ul>
    <router-view></router-view>
  </div>
</template>
<script>
import BannerMsg from '../home/BannerMsg.vue'
import {mapState} from 'vuex'
import backimg1 from '../../assets/img/backimg1.jpg'
import backimg2 from '../../assets/img/backimg2.jpg'
import backimg3 from '../../assets/img/backimg3.jpg'
import backimg4 from '../../assets/img/backimg4.jpg'
import backimg5 from '../../assets/img/backimg5.jpg'

export default {
  data() {
    return {
        contents:[
        {url:"/product/proItem_1", title: "产品方案1", text: "产品方案1产品方案1", imgUrl: backimg1},
        {url:"/product/proItem_2", title: "产品方案2", text: "产品方案2产品方案2", imgUrl: backimg2},
        {url:"/product/proItem_3", title: "产品方案3", text: "产品方案3产品方案3", imgUrl: backimg3},
        {url:"/product/proItem_4", title: "产品方案4", text: "产品方案4产品方案4", imgUrl: backimg4},
        {url:"/product/proItem_5", title: "产品方案5", text: "产品方案5产品方案5", imgUrl: backimg5},
      ],
      num:0
    };
  },
  methods:{
    overTag(index){
      this.num = index
    }
  },
  computed:mapState([
    "bannerData","proitem1Data"
  ]),
  components:{
    BannerMsg
  }
};
</script>
<style>
  .proNav {
    list-style: none;
    padding: 0;
    text-align: center;
    display: flex;
    font-size: 14px;
    margin: 100px auto;
  }
  .proNav li {
    padding: 15px 0;
    width: 20%;
    background-color: #ebebeb;
    cursor: pointer;
  }
  .proNav li.router-link-active{
    background-color: #5298ff;
    color: #fff;
  }
</style>



