<template>
  <div class="itemDetail container">
    <el-row>
      <el-col :span="12" class="itemC itemDleft">
        <div class="grid-content">
          <div class="shows" v-if="num == index" v-for="(item,index) in contents" :key="index">
            <div class="imgback">
              <img :src="item.imgUrl" alt="img" class="img">
            </div>
            <p class="titP">{{item.title}}</p>
            <span class="textS">{{item.text}}</span>
            <span class="dateS">{{item.date}}</span>
          </div>
        </div>
      </el-col>
      <el-col :span="12" class="itemC itemDright">
        <div class="grid-content">
          <ul class="list">
            <li @mousemove="choose(index)" v-for="(item,index) in contents" :key="index" :class="{active:index==num}">
              <p class="index">{{'0'+(index+1)}}</p>
              <div class="rightD">
                <div class="titD">
                  <p class="titP">{{item.title}}</p>
                  <p class="dateP">{{item.date}}</p>
                </div>
                <p class="textP">{{item.text}}</p>
              </div>
            </li>
          </ul>
          <p class="more"> 查看更多 <i data-v-959e2010="" data-v-9be37514="" class="el-icon-arrow-right"></i></p>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import contimg1 from '../../assets/img/contimg1.jpg'
import contimg2 from '../../assets/img/contimg2.jpg'
import contimg3 from '../../assets/img/contimg3.jpg'
import contimg4 from '../../assets/img/contimg4.jpg'
export default {
  data() {
    return {
      contents: [
        { title: "标题title1", text: "内容content1", date: "2018-02-12", imgUrl: contimg1 },
        { title: "标题title2", text: "内容content2", date: "2018-02-09", imgUrl: contimg2 },
        { title: "标题title3", text: "内容content3", date: "2018-03-12", imgUrl: contimg3 },
        { title: "标题title4", text: "内容content4", date: "2018-04-09", imgUrl: contimg4 }
      ],
      num: 0
    };
  },
  methods: {
    choose(index) {
      this.num = index;
    }
  }
};
</script>

<style>
.itemDetail {
  display: flex;
  margin-bottom: 50px;
}
.itemDetail .el-row {
  height: 360px;
  width: 100%;
  overflow: hidden;
}
.itemDetail .itemC {
  height: 360px;overflow: hidden;
  position: relative;
}
/*left*/
.itemDetail .itemDleft .grid-content {
  margin: 20px;
  height: 320px;
}
.itemDetail .imgback {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}
.itemDetail .img {
  width: 100%;
  height: 100%;
}
.itemDetail .imgback:after {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;height: 100%;background-color: rgba(0, 0, 0, 0.35);
  z-index: 10;
}
.itemDetail .itemDleft .grid-content {
    display: flex;
    align-items: flex-end;
    color: #fff;
    font-family: 'microsoft yahei';
}
.itemDetail .titP {
    font-size: 20px;
    margin: 0;
    margin-bottom: 10px;
}
.itemDetail .textS {
    font-size: 12px;
}
.itemDetail .dateS {
    font-size: 12px;
    position: absolute;
    right: 10px;
    bottom: 10px;
}
/*right*/
.itemDetail .list {
  list-style: none;
  padding: 0;
  margin: 0 20px;
}
.itemDetail .list li {
  height: 52px;
  padding: 10px;
  overflow: hidden;
  display: flex;
}
.itemDetail .list li.active {
    background-color: #5298ff;
    cursor: pointer;
    color: #fff;
}
.itemDetail .list li.active p {
  color: #fff;
}
.itemDetail .index {
    width: 50px;
    text-align: center;
    font-size: 18px;
    line-height: 7px;
    margin-right: 6px;
}
.rightD {
    flex: 1;
    position: relative;
}
.itemDright .titP {
    font-size: 16px;
    margin: 2px 0 0;
    overflow: hidden;
    text-overflow: ellipsis;
    width: 170px;
    white-space: nowrap;
    margin-bottom: 10px;
}
.itemDright .dateP {
    width: 130px;
    text-align: right;
    font-size: 12px;
    padding: 0;
    margin: 0;
    position: absolute;
    right: 0;
    top: 0;
}
.itemDright .textP {
    margin: 5px 0;
    color: #737373;
    font-size: 14px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
}
.itemDright .more {
    width: 230px;
    text-align: center;
    margin: 20px auto 0;
    border: 1px solid #ddd;
    padding: 10px 0;
    cursor: pointer;
    font-size: 12px;
}
</style>


