<template>
  <div class="headerWrapper">
    <header class="header">
      <div class="container">
        <!-- logo start -->
        <h1>
          <router-link to="/home">
            <img :src="logo" alt="ljyLogo" class="nav-logo">
          </router-link>
        </h1>
        <!-- logo end -->
        <!-- nav start -->
        <ul class="nav">
          <li class="nav-item" v-for="(item,index) in headerUls" :key="index">
            <router-link class="link" :to="item.url">{{item.text}}</router-link>
          </li>
        </ul>
        <!-- nav end -->
      </div>
    </header>
  </div>
</template>
<script>
import logo from '../assets/img/logo.svg'
export default {
  data() {
    return {
      headerUls:[
        {"text":"首页","url":"/home"},
        {"text":"产品方案","url":"/product"},
        {"text":"关于我们","url":"/about"},
        {"text":"加入我们","url":"/join"}
      ],
      logo
    };
  }
};
</script>
<style>
body{margin: 0;font-family: 'microsoft yahei';margin-top: 50px;}
ul{margin: 0;padding: 0;list-style: none;}
.headerWrapper {
  position: absolute;
  width: 100%;
  left: 0;
  top: 0;
  z-index: 1500;
  height: 50px;
}
.container {
  height: 100%;
  box-sizing: border-box;
  width: 1000px;
  padding: 5px;
  margin: 0 auto;
}
.header {
  height: 50px;
  color: #000;
  width: 100%;
  z-index: 100;
  position: fixed;
  background-color: rgba(255, 255, 255, 0.9);;
  border-bottom: 1px solid #ddd;
}
.header h1 {
  margin: 0;
  float: left;
}
.header h1 a {
  color: #333;
  text-decoration: none;
  display: block;
  cursor: pointer;
}
.nav-logo {
  height: 38px;
}
.header .nav {
  float: right;
  height: 100%;
  line-height: 40px;
  background: transparent;
  padding: 0;
  margin: 0;
}
.header .nav-item {
  margin: 0;
  float: left;
  list-style: none;
  cursor: pointer;
}
.header .nav-item .link {
  text-decoration: none;
  color: #333;
  display: block;
  margin: 0 22px;
  font-size: 15px;
  position: relative;
}
.header .nav-item .link.router-link-active:after {
  content: "";
  display: inline-block;
  position: absolute;
  bottom: 3px;
  left: 0;
  width: 100%;
  height: 3px;
  background: #409eff;
}

</style>




