<template>
  <div class="itemIntro">
    <div class="cont" v-for="(item,index) in contents" :key="index">
      <el-row type="flex" class=" container">
        <el-col :span="12" :push="item.pop">
          <div class="grid-content">
            <p class="title">{{item.title}}</p>
            <p class="text">{{item.text}}</p>
          </div>
        </el-col>
        <el-col :span="12" :pull="item.pop">
          <div class="grid-content img-cont">
            <img class="img" :src="item.imgUrl" alt="img">
          </div>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      
    };
  },
  props:["contents"]
};
</script>

<style>
.itemIntro .cont {
  border-bottom: 1px solid #ddd;
}
.itemIntro .cont:nth-of-type(2n) {
    background: #f6f7f8;
}
.itemIntro .el-row {
  align-items: center;
  padding: 50px 20px;
}
.itemIntro .grid-content {
  margin: 25px;
}
.itemIntro .title {
  color: rgb(29, 44, 84);
  font-size: 22px;
  font-weight: 700;
}
.itemIntro .text {
  color: rgb(87, 105, 154);
  font-size: 18px;
}
.itemIntro .img-cont {
  display: flex;
  justify-content: center;
}
.itemIntro .img {
  height: 210px;
  margin: auto;
}
</style>


