<template>
  <div class="footer">
    <div class="ftDetail container">
      <div class="code">
        <img :src="code" alt="code">
        <p>关注微信公众号</p>
      </div>
      <div class="table">
        <dl v-for="(item,index) in footerUl" :key="index">
          <dt>{{item.dt}}</dt>
          <router-link tag="dd" :to="item.path" v-for="(item,index) in item.children" :key="index">
            {{item.dd}}
          </router-link>
        </dl>
      </div>
    </div>
    <div class="ftCopyright">深圳市XX信息技术有限公司 版权@所有 粤ICP备15043971号-1</div>
  </div>
</template>
<script>
import code from '../assets/img/code.jpg'
export default {
  data() {
    return {
      footerUl:[
        {"dt":"ELEMENT","children":[
          {"dd":"关于我们","path":"/about"}
        ]},
        {"dt":"产品方案","children":[
          {"dd":"产品方案1","path":"/product/proItem_1"},
          {"dd":"产品方案2","path":"/product/proItem_2"},
          {"dd":"产品方案3","path":"/product/proItem_3"}
        ]},
        {"dt":"加入我们","children":[
          {"dd":"招聘信息","path":"/join"}
        ]},
        {"dt":"联系方式","children":[
          {"dd":"联系电话：186-xxxx-xxxx","path":""},
          {"dd":"联系邮箱：support@wxxxx.com","path":""},
          {"dd":"地址：深圳市南山区粤海大道XXXX","path":""}
        ]},
      ],code
    };
  }
};
</script>
<style>
.footer {
  padding: 30px 0;
  background-color: #393c4c;
  color: #8d96ad;
  font-size: 14px;
  font-family: 'microsoft yahei';
}
.footer .ftDetail {
  display: flex;
}
.footer .code {margin-right: 50px;text-align: center;}
.footer .code img {
  width: 120px;
  height: 120px;
}
.footer .table {
  display: flex;
  justify-content:space-between;
  width: 100%;
}
.footer .table dl {
  margin-right: 30px; 
}
.footer .table dt {
  font-size: 16px;
  color: #fff;
  margin-bottom: 15px;
}
.footer .table dd {
  margin: 0;
  margin-bottom: 10px;
  cursor: pointer;
}
.footer .table dd:hover {
  color: #fff;
}
.footer .table dl:nth-of-type(4) dd {
  cursor: inherit;
}
.footer .table dl:nth-of-type(4) dd:hover {
  color: #8d96ad;
}
.footer .ftCopyright {text-align: center;padding-top: 20px;}
</style>




