import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../components/home/Home.vue'
import About from '../components/about/About.vue'
import Join from '../components/join/Join.vue'
import Product from '../components/product/Product.vue'
  import ProItem1 from '../components/product/pro_item/ProItem_1.vue'
  import ProItem2 from '../components/product/pro_item/ProItem_2.vue'
  import ProItem3 from '../components/product/pro_item/ProItem_3.vue'
  import ProItem4 from '../components/product/pro_item/ProItem_4.vue'
  import ProItem5 from '../components/product/pro_item/ProItem_5.vue'
    import BannerMsgs from '../components/home/BannerMsg.vue'
Vue.use(VueRouter)

const router = new VueRouter({
  routes:[
    {path:'/home',component:Home,name:'home'},
    {path:'/about',component:About},
    {path:'/join',component:Join},
    {path:'/product',component:Product,redirect:'/product/proItem_1',children:[
      { path: '/product/proItem_1', components: {default:ProItem1,a:BannerMsgs}},
      { path: '/product/proItem_2', components: {default:ProItem2,a:BannerMsgs}},
      { path: '/product/proItem_3', components: {default:ProItem3,a:BannerMsgs}},
      { path: '/product/proItem_4', components: {default:ProItem4,a:BannerMsgs}},
      { path: '/product/proItem_5', components: {default:ProItem5,a:BannerMsgs}},
    ]},
    {path: '*',redirect:"/home"}
  ],
  scrollBehavior(to, from, savedPosition) {
    window.scrollTo(0, 0);
  }
})
export default router



