<template>
  <div class="proItem1">
    <!-- <div>{{proitem1Data}}</div> -->
    <div class="container pro-title">
      <h1 class="title">{{proitem1Data[2].proTitle.title}}</h1>
      <p class="text">{{proitem1Data[2].proTitle.text}} </p>
    </div>
    <h2 class="small-title">
      ——  私募基金投资会计  ——
    </h2>
    <ul class="pro-class">
      <li v-for="(item,index) in proitem1Data[2].proClass" :key="index">
        <imgItem :item="item"></imgItem>
      </li>
    </ul>
    <h2 class="small-title">
      ——  核心业务  ——
    </h2>
    <div class="container hxyw">
      <imgItem :item="item" v-for="(item,index) in proitem1Data[1].hxyw" :key="index"></imgItem>
    </div>
  </div>
</template>
<script>
import imgItem from './img-item.vue'
import {mapState} from 'vuex'
export default {
  data() {
    return {
        
    };
  },
  computed:mapState([
    "proitem1Data"
  ]),
  components:{
    imgItem
  }
};
</script>
<style>
.proItem1 .pro-title {
  margin-bottom: 50px;
}
.proItem1 .pro-title .title {
  font-size: 22px;
  color: #1d2c54;
  text-align: center;
  font-weight: inherit;
  margin-bottom: 50px;
}  
.proItem1 .pro-title .text {
  text-indent: 2em;
}
.proItem1 .small-title {
  font-size: 20px;
  font-family: PingFangSC-Regular;
  color: #8d96ad;
  font-weight: 400;
  text-align: center;
  margin: 60px 0;
}
.pro-class {
  display: flex;
}
.pro-class li {
  margin: 0 50px 100px;
}

.hxyw {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 50px;
}
.hxyw .img-item {
  width: 50%;
  margin-bottom: 25px;
  padding: 0 50px;
  box-sizing: border-box;
}
.hxyw .text {
  padding: 0 40px;
}
</style>



