<template>
  <div class="bannerMsg">
    <img :src="item.imgUrl" alt="back" class="back">
    <div class="banner-msg container">
      <h1>{{item.title}}</h1>
      <p>{{item.text}}</p>
      <router-link tag="span" class="more" :to="item.url" v-if="item.more">{{item.more}}</router-link>
    </div>
  </div>
</template>
<script>
  export default {
    props:["item"]
  }
</script>
<style>
.bannerMsg {
  height: 500px;position: relative;
}
.bannerMsg .back {
  position: absolute;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}
.bannerMsg .banner-msg {
  padding-top: 180px;
  color: #fff;
  font-family: 'microsoft yahei';
}
.bannerMsg .banner-msg h1 {
  font-size: 38px;
}
.bannerMsg .banner-msg p {
  font-size: 22px;
}
.bannerMsg .banner-msg .more {
  background: #5298ff;
  border-radius: 2px;
  font-size: 12px;
  display: inline-block;
  padding: 10px 20px;
  cursor: pointer;
}
</style>




