<template>
  <div class="img-item">
    <img class="head" :src="item.imgUrl" alt="head">
      <p class="title">{{item.title}}</p>
      <p class="text">{{item.text}}</p>
  </div>
</template>
<script>
  export default {
    props:["item"]
  }
</script>
<style>
.img-item {   
  display: flex;
  flex-direction: column;
  align-items: center;
}
.img-item .head {
  width: 80px;
  height: 72px;
}
.img-item .title {
  color: #131a35;
  font-size: 18px;
  margin: 40px 0;
  font-weight: bold;
}
.img-item .text {
  color: #999;
  line-height: 22px;
}
</style>




