<template>
  <div class="itemTitle">
    <p>{{title}}</p>
    <span>{{smallTitle}}</span>
  </div>
</template>
<script>
export default {
  data() {
    return {
      // title: "我们的产品",
      // smallTitle: "our product"
    };
  },
  props:["title","smallTitle"]
};
</script>
<style>
.itemTitle {
  text-align: center;
  padding: 10px;
}
.itemTitle * {
  margin: 0;
}
.itemTitle p {
  font-size: 26px;
  margin-top: 10px;
  color: #1d2c54;
  font-family: "PingFangSC-Regular";
}
.itemTitle p:after {
  content: "";
  display: block;
  border-bottom: 1px solid #c1c1c1;
  width: 80px;
  margin: 6px auto;
}
.itemTitle span {
  font-size: 14px;
  text-transform: uppercase;
}
</style>




