<template>
  <div class="itemEx">
    <el-carousel :interval="7000" arrow="always" indicator-position="none" height="220px" class="container">
      <el-carousel-item v-for="(item,index) in contents" :key="index">
        <ul class="cap">
          <li v-for="(item,index) in item" :key="index">
            <img :src="item.imgUrl" alt="img">
          </li>
        </ul>
      </el-carousel-item>
    </el-carousel>
  </div>
</template>

<script>
import ex1 from '../../assets/img/ex1.png'
import ex2 from '../../assets/img/ex2.png'
import ex3 from '../../assets/img/ex3.png'
import ex4 from '../../assets/img/ex4.png'
import ex5 from '../../assets/img/ex5.png'
import ex6 from '../../assets/img/ex6.png'
import ex7 from '../../assets/img/ex7.png'
import ex8 from '../../assets/img/ex8.png'
import ex9 from '../../assets/img/ex9.png'
export default {
  data() {
    return {
      contents: [
        [
          { imgUrl: ex1},
          { imgUrl: ex2},
          { imgUrl: ex3},
          { imgUrl: ex4},
          { imgUrl: ex5},
          { imgUrl: ex6}
        ],
        [
          { imgUrl: ex7},
          { imgUrl: ex8},
          { imgUrl: ex9}         
        ]
      ]
    };
  }
};
</script>

<style>
.itemEx {
  padding: 50px 0;
}
.itemEx .el-carousel__container {overflow: hidden;}
.itemEx .el-carousel__arrow {
  width: 60px;
  height: 100%;
  border-radius: 0;
  background: #fff;
  color: #a9a9a9;
  font-size: 20px;
}
.itemEx .el-carousel__arrow i {
    cursor: pointer;
    border: 1px solid #a9a9a9;
    padding: 20px 5px;
}
.itemEx .el-carousel__arrow--left {
    left: 0px;
}
.itemEx .el-carousel__arrow--right {
    right: 0;
}
.itemEx .el-carousel__item {
  width: auto;
  height: 100%;
  top: 0;
  left: 80px;
  right: 80px;
  position: absolute;
}
.itemEx .cap {
  margin: 0;
  height: 100%;
  display: flex;
  flex-wrap: wrap;
  align-content: space-around;
  padding: 0;
}
.itemEx .cap li {
  width: 30%;
  height: 40%;
  display: inline-block;
  margin: auto;
  display: flex;
}
.itemEx .cap li img {
  margin: auto;
}
</style>


