<template>
  <div class="product">
    <BannerMsg :item="item" v-for="(item,index) in contents" :key="index"></BannerMsg>
    <ItemTitle title="关于我们" smallTitle="about us"></ItemTitle>
    <div class="content container">
      <p>深圳市尚诚信息技术有限公司成立于2015年，公司主要经营计算机软，硬件开发及应用,</p>
      <p>是一家金融科技企业，基于大数据、云计算以及移动互联技术，向金融行业提供全新的资产管理与财富管理IT解决方案，</p>
      <p>公司团队多来自于行业上市公司或资深金融机构，具备多年大中型金融IT的建设经验，</p>
      <p>已成功研发出了包括帕拉丁基金运营系统、基金销售与客户管理系统、投资会计和风控系统，</p>
      <p>互联网金融运营管理系统等产品方案，覆盖了公募基金、券商托管和外包、私募基金、保险资管等行业客户。</p>
      <p>公司秉承“为客户创造价值、以技术服务为发展根本”的经营宗旨，持续用优质的产品与服务，为金融机构的创新发展提供驱动力。</p>
    </div>
    <ItemTitle title="发展历程" smallTitle="development history"></ItemTitle>
    <img :src="about" alt="" class="about">
  </div>
</template>
<script>
import BannerMsg from '../home/BannerMsg.vue'
import ItemTitle from '../home/Item_title.vue'
import backimg2 from '../../assets/img/backimg2.jpg'
import about from '../../assets/img/about.png'
export default {
  data() {
    return {
        contents:[
        { title: "关于我们", text: "关于我们关于我们关于我们", imgUrl: backimg2}
      ],about
    };
  },
  components:{
    BannerMsg,ItemTitle
  }
};
</script>
<style>
.content {
  text-align: left;
  font-size: 14px;
  margin-bottom: 80px;
}
.about {
  display: flex;
  width: 50%;
  margin: 50px auto;
}
</style>



